import appConstants from "shared/constants/app";

export function setCookie(
  name: string,
  value: string | number,
  options: {
    days?: number;
    path?: string;
  } = {}
): void {
  const { days, path = "/" } = options;
  let expiry = "";

  if (days) {
    const date = new Date();

    date.setTime(date.getTime() + days * appConstants.time.milliSecondsPerDay);

    expiry = `; expires=${date.toUTCString()}`;
  }

  document.cookie = `${name}=${value}${expiry}; path=${path}`;
}

export function getCookie(name: string): string {
  const cookieMatch = document.cookie.match(`(^|;)\\s*${name}\\s*=\\s*([^;]+)`);

  return (cookieMatch ? cookieMatch.pop() : "")!;
}

export function deleteCookie(name: string): void {
  setCookie(name, "", { days: -1 });
}
